$cl-point-d: #000;

$cl-point-change: #ff5de6;

$ctaColor: #ff409f;

// PROJECT グラデーション -------------------------------------------------------

@mixin gr-key($deg: -135) {
	background-image: linear-gradient(#{$deg}deg, #1d4c94 0%, #4887ff 100%);
}

@mixin gr-key-change($deg: 45) {
	background-image: linear-gradient(#{$deg}deg, #27c488 0%, #50b9e8 100%);
}

@mixin gr-key-l($deg: 45) {
	background-image: linear-gradient(#{$deg}deg, #dfe9f7 0%, #dff7e9 100%);
}

// PROJECT シャドウ -------------------------------------------------------

@mixin shadow-key($x: 0, $y: rem(10), $size: rem(20), $opacity: 0.2) {
	box-shadow: $x $y $size rgba(72, 135, 255, $opacity);
}

@mixin shadow-black($x: 0, $y: 0, $size: rem(40), $opacity: 0.2) {
	box-shadow: $x $y $size rgba(0, 0, 0, $opacity);
}

// 場所依存 -------------------------------------------------------

//基本上記のものを使って指定。直接色を書かない

//body要素に色をつける場合
$cl-body: $cl-base;

