.c-decoration {
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  padding: 1rem;
  font-size: 0.5rem;
  line-height: 1;
  opacity: 0.5;
}

.c-decoration.-reverse {
  right: 0;
  left: auto;
}

@media screen and (min-width: 700px) {
  .c-decoration {
    padding: 1.5rem;
  }
}

.c-decoration_text {
  position: relative;
  padding-bottom: 5em;
  writing-mode: vertical-lr;
}

.c-decoration_text::after {
  position: absolute;
  bottom: 0;
  left: 0.5em;
  display: block;
  width: 0.0625rem;
  height: 4em;
  content: '';
  background-color: #000;
}

.c-angle.-set {
  position: relative;
}

.c-angle.-set::before {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 3.5em;
  height: 3.5em;
  content: '';
  border-top: 1.5em solid #1a1a1a;
  border-left: 1.5em solid #1a1a1a;
}

.c-angle.-set::after {
  position: absolute;
  right: 0;
  bottom: 0;
  display: block;
  width: 3.5em;
  height: 3.5em;
  content: '';
  border-right: 1.5em solid #1a1a1a;
  border-bottom: 1.5em solid #1a1a1a;
}

.c-angle {
  position: relative;
}

.c-angle.-tl::before {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
  display: block;
  width: 3.5em;
  height: 3.5em;
  content: '';
  border-top: 1.5em solid #4887ff;
  border-left: 1.5em solid #4887ff;
}

.c-angle.-br::after {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 5;
  display: block;
  width: 3.5em;
  height: 3.5em;
  content: '';
  border-right: 1.5em solid #4887ff;
  border-bottom: 1.5em solid #4887ff;
}

.balloonLine {
  position: relative;
}

.balloonLine::before, .balloonLine::after {
  position: absolute;
  top: 0;
  display: block;
  width: 0.25rem;
  height: 2em;
  content: '';
  background-color: currentColor;
}

.balloonLine::before {
  left: 0;
  transform: rotate(-30deg);
}

.balloonLine::after {
  right: 0;
  transform: rotate(30deg);
}

.c-quote {
  position: relative;
}

.c-quote.-tr::before {
  font-family: 'Noto Sans JP', sans-serif;
  position: absolute;
  top: -20%;
  right: 5%;
  z-index: 5;
  display: block;
  width: 6.25rem;
  height: 6.25rem;
  font-size: 10rem;
  line-height: 1;
  color: #4887ff;
  text-align: right;
  content: '”';
}

.c-icon {
  display: inline-block;
  width: 1.1em;
  height: 1.1em;
  vertical-align: middle;
  fill: currentColor;
}

.c-hamburger {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 50%;
  height: 50%;
  margin: auto;
  vertical-align: middle;
}

.c-hamburger_line {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 0.125rem;
  margin: auto;
  background-color: transparent;
  border-radius: 0;
  transition: 150ms;
  transform-origin: left;
}

.c-hamburger_line::before, .c-hamburger_line::after {
  position: absolute;
  display: block;
  height: 100%;
  content: '';
  background-color: currentColor;
  border-radius: 0;
}

.c-hamburger_line::before {
  top: 0;
  width: 100%;
  margin-top: -16%;
}

.c-hamburger_line::after {
  bottom: 0;
  width: 64%;
  margin-bottom: -16%;
}

