  height: 100%;
  border-top: 0.1875rem solid currentColor;
  border-right: 0.1875rem solid currentColor;
  transform: rotate(45deg);
}

.c-arrow.-left {
  transform: rotate(225deg);
}

.c-arrow.-top {
  transform: rotate(-45deg);
}

.c-arrow.-bottom {
  transform: rotate(135deg);
}

