.c-arrowRound::before, .c-arrowRound::after {
  position: absolute;
  width: 0.5rem;
  height: 0.5rem;
  content: '';
  background-color: currentColor;
  border-radius: 6.25rem;
}

.c-arrowRound::before {
  top: -0.5rem;
  left: -0.25rem;
}

.c-arrowRound::after {
  right: -0.5rem;
  bottom: -0.25rem;
}

.c-arrowRound.-left {
  transform: rotate(225deg);
}

.c-arrowRound.-top {
  transform: rotate(-45deg);
}

.c-arrowRound.-bottom {
