/* button common */
.btn {
  font-style: normal;
  font-weight: 700;
  line-height: 1.8;
  text-align: center;
  text-indent: 0.1em;
  letter-spacing: 0.1em;
  font-feature-settings: 'palt' 1;
  font-family: 'Noto Sans JP', sans-serif;
  display: block;
  margin-right: auto;
  margin-left: auto;
  font-size: 1.2rem;
  cursor: pointer;
}

.btn_text::before {
  display: block;
  width: 0;
  height: 0;
  margin-top: calc((1 - 1.8) * 0.5em);
  content: '';
}

.btn_text::after {
  display: block;
  width: 0;
  height: 0;
  margin-bottom: calc((1 - 1.8) * 0.5em);
  content: '';
}

.btn_link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.btn_icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-left: 0.2em;
  fill: currentColor;
}

.btn_icon span {
  font-size: 1.2em;
}

.btnBox {
  display: block;
  width: 100%;
  max-width: 25rem;
  margin-top: 3.5rem;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
  transition: 200ms;
  transition-timing-function: ease;
  transform: translateY(0);
}

.btnBox:hover {
