/* heading -------------------------------------------------- */
.headingAbout {
  padding-top: 1rem;
  padding-right: 0.625rem;
  padding-left: 0.625rem;
  font-size: 2rem;
  font-weight: 900;
  line-height: 1.5;
  color: #fff;
  text-align: center;
  letter-spacing: 0.05;
}

.headingAbout::before {
  display: block;
  width: 0;
  height: 0;
  margin-top: calc((1 - 1.5) * 0.5em);
  content: '';
}

.headingAbout::after {
  display: block;
  width: 0;
  height: 0;
  margin-bottom: calc((1 - 1.5) * 0.5em);
  content: '';
}

@media screen and (min-width: 700px) {
  .headingAbout {
    font-size: 3.5rem;
  }
}

@media screen and (min-width: 1200px) {
  .headingAbout {
    padding-top: 2.5rem;
    font-size: 5rem;
  }
}

