/* loading -------------------------------------------------- */
.loading {
  position: fixed;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background-color: #4887ff;
  opacity: 1;
  transition: all 150ms;
}

.loading.-loaded {
  pointer-events: none;
  opacity: 0;
}

.js-disabled .loading {
  pointer-events: none;
  opacity: 0;
}

.sk-folding-cube {
  position: relative;
  width: 40px;
  height: 40px;
  margin: 20px auto;
  transform: rotateZ(45deg);
}

.sk-folding-cube .sk-cube {
  position: relative;
  float: left;
  width: 50%;
  height: 50%;
  transform: scale(1.1);
}

.sk-folding-cube .sk-cube::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: '';
  background-color: #fff;
  transform-origin: 100% 100%;
  animation: sk-foldCubeAngle 2.4s infinite linear both;
}

